import { useStaticQuery, graphql } from "gatsby"

export const useShopProducts = () => {
  const { allMdx } = useStaticQuery(
    graphql`
      query shopProducts {
        allMdx(
          filter: { slug: { regex: "/shop//" } }
          sort: { fields: slug, order: ASC }
        ) {
          edges {
            node {
              id
              slug
              excerpt
              body
              frontmatter {
                title
                categories
                slug
                tags
                cover {
                  childImageSharp {
                    gatsbyImageData(
                      quality: 100
                      placeholder: TRACED_SVG
                      formats: [AUTO, WEBP, AVIF]
                    )
                  }
                }
                extra {
                  amount
                  accauntLink
                  mail
                  phone
                  price
                  server
                  vehicles {
                    premium
                    x
                  }
                }
              }
            }
          }
        }
      }
    `
  )
  return allMdx.edges
}
